.item-list {
	width: 60%;
	margin: auto;
	margin-top: 20px;
	margin-bottom: 10px;
	list-style: none;
	text-align: left;
	background-color: #fff;
	border: 1px solid #d6d6d6;
	padding: 0;
	border-radius: 8px;
	transition: all 0.5s;
}

.item-list.disable {
	pointer-events: none;
	opacity: 0.3;
}
